import React from "react"
import { Row, Col, Card, CardHeader, CardBody, Button } from "reactstrap"
import Radio from "../../components/@vuexy/radio/RadioVuexy"
import Checkbox from "../../components/@vuexy/checkbox/CheckboxesVuexy"
import { Check } from "react-feather"
import Select from "react-select"
import Flatpickr from "react-flatpickr";
import { X } from "react-feather"
import PerfectScrollbar from "react-perfect-scrollbar"
import { EditorState } from "draft-js"
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css"
import "../../assets/scss/plugins/extensions/editor.scss"
import "flatpickr/dist/themes/light.css";
import "../../assets/scss/plugins/forms/flatpickr/flatpickr.scss"

const colourOptions = [
    { value: "0", label: "Active Deals" },
    { value: "1", label: "Deleted Deals" },
    { value: "2", label: "All Deals" },
]

const monthOptions = [
    { value: "1", label: "01 Jan" },
    { value: "2", label: "02 Feb" },
    { value: "3", label: "03 Mar" },
    { value: "4", label: "04 Apr" },
    { value: "5", label: "05 May" },
    { value: "6", label: "06 Jun" },
    { value: "7", label: "07 Jul" },
    { value: "8", label: "08 Aug" },
    { value: "9", label: "09 Sep" },
    { value: "10", label: "10 Oct" },
    { value: "11", label: "11 Nov" },
    { value: "12", label: "12 Dec" },
]

const yearOptions = [
    { value: "0", label: "2020" },
    { value: "1", label: "2019" },
    { value: "2", label: "2018" },
    { value: "3", label: "2017" },
    { value: "4", label: "2016" },
    { value: "5", label: "2015" },
    { value: "6", label: "2014" },
    { value: "7", label: "2013" },
    { value: "8", label: "2012" },
    { value: "9", label: "2011" },
    { value: "10", label: "2010" },
    { value: "11", label: "2009" },
    { value: "12", label: "2008" },
]

class ComposeEmail extends React.Component {
    state = {
        editorState: EditorState.createEmpty(),
        basicPicker : new Date(),
        endPicker: new Date(),

        selectedOption: "",
    }

    onRadioChange = (event) => {
        this.setState({
            selectedOption: event.target.value
        })
    }

    handleSidebarClose = () => {
        this.props.handleComposeSidebar("close")
        this.setState({
            emailTo: "",
            emailSub: "",
            emailCC: "",
            emailBCC: "",
            editorState: EditorState.createEmpty()
        })
    }

  render() {
    const { basicPicker, endPicker } = this.state
    const labelStyle = {
        paddingTop: "15px",
        color: "#0c0000",
        fontSize: "13px",
    }
    return (
    <Card className={`compose-email ${ this.props.currentStatus ? "open" : "" }`} style={{zIndex: "11"}}>
        <CardHeader className="compose-mail-header align-items-center">
            <div className="compose-mail-title">
                <h3 className="text-bold-600 card-title">Customize Report</h3>
            </div>
            <div className="close-compose-mail" onClick={() => this.props.handleComposeSidebar("close")} >
                <X size={20} />
            </div>
        </CardHeader>
        <PerfectScrollbar options={{ wheelPropagation: false }} >
            <CardBody className="compose-mail-body p-1">
                <Radio
                    label="Month to Date"
                    color="primary"
                    defaultChecked={true}
                    name="exampleRadioSizes"
                    className="py-50"
                    value="MonthToDate"
                    onChange={this.onRadioChange}
                />
                <Radio
                    label="Today"
                    color="primary"
                    defaultChecked={false}
                    name="exampleRadioSizes"
                    className="py-50"
                    value="Today"
                    onChange={this.onRadioChange}
                />
                <Radio
                    label="Custom Range"
                    color="primary"
                    defaultChecked={false}
                    name="exampleRadioSizes"
                    className="py-50"
                    value="CustomRange"
                    onChange={this.onRadioChange}
                />
                <Radio
                    label="All Time"
                    color="primary"
                    defaultChecked={false}
                    name="exampleRadioSizes"
                    className="py-50"
                    value="AllTime"
                    onChange={this.onRadioChange}
                />
                <Radio
                    label="Previous Month"
                    color="primary"
                    defaultChecked={false}
                    name="exampleRadioSizes"
                    className="py-50"
                    value="PreviousMonth"
                    onChange={this.onRadioChange}
                />
                <Radio
                    label="Yesterday"
                    color="primary"
                    defaultChecked={false}
                    name="exampleRadioSizes"
                    className="py-50"
                    value="Yesterday"
                    onChange={this.onRadioChange}
                />
                <Radio
                    label="Select Month"
                    color="primary"
                    defaultChecked={false}
                    name="exampleRadioSizes"
                    className="py-50"
                    value="SelectMonth"
                    onChange={this.onRadioChange}
                />
                <Row>
                    <Col sm="6">
                        <Select
                            className="React"
                            classNamePrefix="select"
                            defaultValue={monthOptions[0]}
                            name="color"
                            options={monthOptions}
                            isDisabled = {this.state.selectedOption === "SelectMonth" ? false : true}
                        />
                    </Col>
                    <Col sm="6">
                        <Select
                            className="React"
                            classNamePrefix="select"
                            defaultValue={yearOptions[0]}
                            name="color"
                            options={yearOptions}
                            isDisabled = {this.state.selectedOption === "SelectMonth" ? false : true}
                        />
                    </Col>
                </Row>
                <Row>
                    <Col sm="6">
                        <h5 className="text-bold-500" style = {labelStyle} >Basic</h5>
                        <Flatpickr
                            className="form-control"
                            value={basicPicker}
                            onChange={date => {
                                this.setState({ basicPicker : date });
                            }}
                        />
                    </Col>
                    <Col sm="6">
                        <h5 className="text-bold-500" style = {labelStyle}>End</h5>
                        <Flatpickr
                            className="form-control"
                            value={endPicker}
                            onChange={date => {
                                this.setState({ endPicker : date });
                            }}
                        />
                    </Col>
                </Row>
                <br />
                <Checkbox
                    color="primary"
                    icon={<Check className="vx-icon" size={16} />}
                    label="New Car"
                    defaultChecked={false}
                />
                <h5 style = {labelStyle}>Deleted</h5>
                <Select
                    className="React"
                    classNamePrefix="select"
                    defaultValue={colourOptions[0]}
                    name="color"
                    options={colourOptions}
                />
                <div className="action-btns d-flex justify-content-end mt-1">
                  <Button.Ripple
                    color="danger"
                    className="mr-1"
                    onClick={() => this.handleSidebarClose()}
                  >
                    Cancel
                  </Button.Ripple>
                  <Button.Ripple
                    color="primary"
                    // disabled={this.state.emailTo.length && this.state.emailBody.length > 0 ? false : true}
                    onClick={() => this.handleSidebarClose()}
                  >
                    Submit
                  </Button.Ripple>
                </div>
            </CardBody>
        </PerfectScrollbar>
    </Card>
    )
  }
}

export default ComposeEmail
